.slider-list {
  @include tablet {
    padding-top: 40px;
  }
}

.slider-list__item {
  display: none;
}

.slider-list__item--active {
  display: block;
}

.slider-list__description {
  margin-bottom: 18px;
  width: 260px;
  line-height: 30px;
}

.slider-list__name {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  margin-bottom: 4px;
  display: block;

  @include tablet {
    margin-top: 61px;
    font-size: 18px;
    line-height: 24px;
  }
  @include desktop {
    .slider-list__name {
      margin-top: 44px;
      font-size: 17px;
    }
  }
}

.slider-list__email {
  font-size: 17px;
  line-height: 30px;
  display: block;
  margin-bottom: 30px;
}

