.radio__label {
  font-weight: 700;
  font-size: 17px;
  line-height: 18px;
  position: relative;
  padding-left: 59px;
  display: flex;
  align-items: center;
  padding-top: 4px;
  min-height: 40px;
  max-width: 216px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .radio__label {
    padding-left: 79px;
    min-height: 46px;
    max-width: 355px;
    font-size: 19px;
    line-height: 18px;
  }
}

.radio__label::before {
  content: "";
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid #444444;
  position: absolute;
  top: 0;
  left: 0;
}
@media (min-width: 768px) {
  .radio__label::before {
    width: 52px;
    height: 52px;
  }
}

.radio__label::after {
  content: "";
  border-radius: 50%;
  background-color: #62d1ba;
  position: absolute;
  width: 16px;
  height: 16px;
  top: 12px;
  left: 12px;
  display: none;
}
@media (min-width: 768px) {
  .radio__label::after {
    width: 22px;
    height: 22px;
    top: 15px;
    left: 15px;
  }
}

.radio__input:checked + .radio__label::after {
  display: block;
}

.radio__input:hover + .radio__label::before {
  border-color: #62d1ba;
}

.radio__input:focus + .radio__label::before {
  border-color: #62d1ba;
  opacity: 0.3;
}

