.nav-button {
  position: absolute;
  width: 20px;
  height: 14px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.nav {
  position: relative;
  text-align: center;
}

.nav--with-js {
  position: absolute;
  width: 100%;
  background: #ffffff;
}

.nav__toggle {
  top: -48px;
  right: 30px;
  display: none;
  background: url("../img/header/menu-close.svg") no-repeat 0 0;
  cursor: pointer;
  width: 21px;
  height: 21px;
}

.nav--with-js .nav__toggle {
  display: block;
}

.nav--closed .nav__toggle {
  background: url("../img/header/menu.svg") no-repeat 0 0;
}

.nav__list {
  margin-left: -67px;
}

.nav--closed .nav__list {
  display: none;
}

@media (min-width: 768px) {
  .nav--with-js {
    position: relative;
    background-color: transparent;
  }
  .nav--with-js .nav__toggle,
  .nav__toggle {
    display: none;
  }
  .nav--closed .nav__list,
  .nav__list {
    display: grid;
    grid-template-columns: 167px 152px minmax(84px, auto) minmax(298px, auto);
    grid-template-rows: minmax(88px, auto) minmax(45px, auto);
    margin: 0;
  }
}
@media (min-width: 1150px) {
  .nav--closed .nav__list,
  .nav__list {
    display: grid;
    grid-template-columns: 220px 183px 298px minmax(82px, auto) minmax(290px, auto);
    grid-template-rows: minmax(88px, auto) minmax(45px, auto);
    margin: 0;
  }
}
