.map {
  position: relative;
  height: 454px;
  width: 100%;
  @include tablet {
    position: relative;
    height: 454px;
    width: 100%;
    grid-row: 2/3;
    grid-column: 1/3;
  }
}

.map__google-wrap {
  position: relative;
  z-index: 5;
  width: 100%;
  height: 100%;
}


.map__iframe {
  width: 100%;
  height: 100%;
}

.map__image-wrap {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 1;
  top: 0;
  left: 0;
}

.map__image {
  display: block;
  width: 100%;
  height: auto;
}
